@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Karla:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
body {
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #0B2434;
    padding: 20px;
    font-family: 'Karla', sans-serif;
}


.die-face {
    height: 60px;
    width: 60px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    cursor: pointer;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    place-items: center;
}

.dot {
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color:black;
 }

.face-1 span:nth-child(1) {
    grid-column: 2;
    grid-row: 2;
}

.face-2 span:nth-child(1) {
    grid-column: 1;
    grid-row: 1;
}

.face-2 span:nth-child(2) {
    grid-column: 3;
    grid-row: 3;
}

.face-3 span:nth-child(1) {
    grid-column: 1;
    grid-row: 1;
}

.face-3 span:nth-child(2) {
    grid-column: 3;
    grid-row: 3;
}

.face-3 span:nth-child(3) {
    grid-column: 2;
    grid-row: 2;
}

.face-4 span:nth-child(1) {
    grid-column: 1;
    grid-row: 1;
}

.face-4 span:nth-child(2) {
    grid-column: 3;
    grid-row: 1;
}

.face-4 span:nth-child(3) {
    grid-column: 1;
    grid-row: 3;
}

.face-4 span:nth-child(4) {
    grid-column: 3;
    grid-row: 3;
}

.face-5 span:nth-child(1) {
    grid-column: 1;
    grid-row: 1;
}

.face-5 span:nth-child(2) {
    grid-column: 3;
    grid-row: 1;
}

.face-5 span:nth-child(3) {
    grid-column: 2;
    grid-row: 2;
}

.face-5 span:nth-child(4) {
    grid-column: 1;
    grid-row: 3;
}

.face-5 span:nth-child(5) {
    grid-column: 3;
    grid-row: 3;
}

.face-6 span:nth-child(4) {
    grid-column: 1;
    grid-row: 3;
}

.face-6 span:nth-child(5) {
    grid-column: 2;
    grid-row: 3;
}

.face-6 span:nth-child(6) {
    grid-column: 3;
    grid-row: 3;
}

main {
    background-color: #F5F5F5;
    height: 500px;
    max-width: 800px;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.title {
    font-size: 40px;
    margin: 0;
}

.instructions {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
}

.dice-container {
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 40px;
}

.roll-dice {
    height: 50px;
    width: 150px;
    border: none;
    border-radius: 6px;
    background-color: #5035FF;
    color: white;
    font-size: 1.2rem;
    font-family: 'Karla', sans-serif;
    cursor: pointer;
}

.roll-dice:focus {
    outline: none;
}

.roll-dice:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
